import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';
import { GlobalService } from 'src/app/core/services/global/global.service';

@Component({
  selector: 'app-dcr-presentation',
  templateUrl: './dcr-presentation.page.html',
  styleUrls: ['./dcr-presentation.page.scss'],
})
export class DcrPresentationPage implements OnInit {
  public productsdata: any;
  private segmentList: any = []
private selectedSegment: any = {p_name:''};
@ViewChild('slide') slide: IonSlides;
  constructor(public global : GlobalService) { }

  ngOnInit() {
    this.global.Seeproducts.subscribe(res=>{
      this.productsdata = res;
      console.log(this.productsdata);
      for(let i=0; i<this.productsdata.length; i++) {
        this.segmentList.push({p_name:this.productsdata[i].p_name});
        console.log(this.segmentList);
      }
    });
  }

  ngOnDestroy() {
    for(let i=0; i<this.productsdata.length; i++) {
      this.segmentList.pop();
    }
    console.log(this.segmentList)
  }
 
  
  segmentChanged(ev: any, index: any) {
    // console.log('Segment changed', ev, index);
  }

  _segmentSelected(p_name: any, index: number) {
    console.log(p_name, index);
    this.slide.slideTo(index)
  }

  _ionSlideDidChange(event: any) {
    console.log(event)
    this.slide.getActiveIndex().then(index => {
      console.log(index)
      this.selectedSegment.p_name = this.segmentList[index].p_name;
      console.log(this.selectedSegment.p_name)
    })
  }


}
